import styled from "styled-components/macro";
import { useState } from "react";
import Step1 from "./Step1";
import Step2 from "./Step2";
import Step3 from "./Step3";

const Wrapper = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
`;

const Header = styled.header`
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  width: 100%;
  height: 50px;
  border-radius: 5px 5px 0 0;
  background: rgb(255, 255, 255);
  background: linear-gradient(
    180deg,
    rgba(255, 255, 255, 0.6) 0%,
    rgba(255, 255, 255, 0) 100%
  );
`;

const StyledH1 = styled.h1`
  margin-top: 10px;
  font-size: 1.4rem;
`;

const SignUpForm = () => {
  const [step, setStep] = useState("step1");

  const stepsEnums = {
    step1: <Step1 setStep={setStep} />,
    step2: <Step2 setStep={setStep} />,
    step3: <Step3 setStep={setStep} />,
  };

  const EnumState = ({ state }) => {
    return <>{stepsEnums[state]}</>;
  };

  console.log(EnumState(step));
  return (
    <Wrapper>
      <Header>
        <StyledH1>Create new account</StyledH1>
      </Header>
      <EnumState state={step}></EnumState>
    </Wrapper>
  );
};

export default SignUpForm;
