import styled from "styled-components/macro";
import {
  StyledForm,
  StyledField,
  Button,
  ErrorBox,
  Error,
  Label,
} from "./SignInForm";
import { Formik, ErrorMessage } from "formik";
import * as Yup from "yup";

const Wrapper = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  padding: 10px;
`;

const StyledStepForm = styled(StyledForm)`
  padding: 0;
  margin: 0;
`;

const Step1 = () => {
  return (
    <Wrapper>
      <Formik
        initialValues={{
          email: "",
          password: "",
          passwordConfirmation: "",
        }}
        validationSchema={Yup.object({
          email: Yup.string()
            .email("Invalid email address")
            .required("Required"),
          password: Yup.string()
            .min(6, "Must be at least 6 characters")
            .required("Required"),
          passwordConfirmation: Yup.string().oneOf(
            [Yup.ref("password"), null],
            "Passwords don't match"
          ),
        })}
        onSubmit={(values, { setSubmitting }) => {
          setTimeout(() => {
            alert(JSON.stringify(values, null, 2));
            setSubmitting(false);
          }, 400);
        }}
      >
        <StyledStepForm autoComplete="off" noValidate>
          <Label htmlFor="email">Email</Label>
          <StyledField name="email" type="email" />
          <ErrorBox>
            <ErrorMessage component={Error} name="email" />
          </ErrorBox>
          <Label htmlFor="password">Password</Label>
          <StyledField name="password" type="password" />
          <ErrorBox>
            <ErrorMessage component={Error} name="password" />
          </ErrorBox>
          <Label htmlFor="passwordConfirmation">Repeat password</Label>
          <StyledField name="passwordConfirmation" type="password" />
          <ErrorBox>
            <ErrorMessage component={Error} name="passwordConfirmation" />
          </ErrorBox>
          <Button type="submit">Next</Button>
        </StyledStepForm>
      </Formik>
    </Wrapper>
  );
};

export default Step1;
